@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat/montserrat-regular.woff2"), url("../fonts/montserrat/montserrat-regular.woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat/montserrat-medium.woff2"), url("../fonts/montserrat/montserrat-medium.woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat/montserrat-semibold.woff2"), url("../fonts/montserrat/montserrat-semibold.woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat/montserrat-bold.woff2"), url("../fonts/montserrat/montserrat-bold.woff");
  font-display: swap;
}
@font-face {
  font-family: "MontserratAlternates";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserratalternates/montserratalternates-regular.woff2"), url("../fonts/montserratalternates/montserratalternates-regular.woff");
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/inter/inter-regular.woff2"), url("../fonts/inter/inter-regular.woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 375px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.6);
  background-color: #ffffff;
}

.container {
  width: 100%;
  max-width: 1120px;
  padding: 0 16px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}

.main-header__wrapper {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    min-height: unset;
  }
}

.main-header__contacts {
  margin-top: auto;
  z-index: 3;
}

.main-header__contacts-box {
  display: flex;
  flex-direction: column;
}

.main-header__contacts-item {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
}

.main-header__contacts-img {
  margin-right: 12px;
}

.main-header__contacts-text {
  font-style: normal;
  color: rgba(0, 0, 0, 0.8);
}

.main-header__contacts-socials {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
  padding: 24px 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.main-header__contacts-social {
  display: flex;
  align-items: center;
}

.main-nav__wrapper {
  z-index: 3;
  flex-direction: column;
}
@media (max-width: 767px) {
  .main-nav__wrapper {
    position: absolute;
    display: none;
    left: 0;
    right: 0;
    padding: 0 16px;
    min-height: 588px;
    background-color: #ffffff;
  }
}

.main-nav__wrapper--opened {
  display: flex;
}
.main-nav__wrapper--opened .main-nav__toggle-line:nth-child(1) {
  transform: rotate(45deg) translateX(-50%);
}
.main-nav__wrapper--opened .main-nav__toggle-line:nth-child(2) {
  transform: rotate(-45deg) translateX(-50%);
}

.main-nav__top-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
}

.main-nav__list {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 24px;
  margin: 0;
  padding: 36px 0 0 0;
  list-style: none;
  z-index: 3;
}

.main-nav__link {
  display: block;
  font-size: 18px;
  line-height: 22px;
  color: rgba(0, 0, 0, 0.8);
}

.main-nav__toggle {
  position: relative;
  width: 24px;
  height: 24px;
  cursor: pointer;
  background-color: transparent;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle:disabled {
  opacity: 0.3;
}

.main-nav__toggle-line {
  display: block;
  position: absolute;
  left: 50%;
  width: 16px;
  height: 2px;
  background-color: #6f767e;
  transition: 0.3s ease;
  transform: translateX(-50%);
}
.main-nav__toggle-line:nth-child(1) {
  top: 7px;
}
.main-nav__toggle-line:nth-child(2) {
  top: 14px;
}